/* General container styling */
.section-center {
  width: 90vw;
  max-width: 600px;
  margin: 5rem auto;
  background: #f0f4f8;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

h4 {
  text-align: center;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #333;
  letter-spacing: 1px;
}

/* Form styling */
.form-control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.form-input {
  flex: 1;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  font-size: 1rem;
}

.form-input:focus {
  outline: none;
  border-color: #5a67d8;
  box-shadow: 0 0 5px rgba(90, 103, 216, 0.5);
}

.btn {
  padding: 0.75rem 1.5rem;
  background-color: #5a67d8;
  color: white;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #434190;
}

/* Items styling */
.items {
  margin-top: 2rem;
}

.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #e2e8f0;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  margin-bottom: 0.5rem;
}

.item input[type='checkbox'] {
  margin-right: 1rem;
}

.item p {
  flex: 1;
  margin: 0;
  color: #333;
}

/* Delete button styling */
.delete-btn {
  background-color: #ff5c5c;
  color: white;
  border: none;
  border-radius: 0.25rem;
  padding: 0.25rem 0.75rem;
  cursor: pointer;
}

.delete-btn:hover {
  background-color: #e53e3e;
}

/* Background theme */
body {
  background-color: #edf2f7;
  font-family: 'Arial', sans-serif;
  padding: 0;
  margin: 0;
}
